import { html } from "@microsoft/fast-element";
import { renderComponent } from "../../__test__/helpers.js";
const storyTemplate = html `
    <fast-data-grid
        style="${x => x.style}"
        :rowsData="${x => x.rowsData}"
        no-tabbing="${x => x.noTabbing}"
        generate-header="${x => x.generateHeader}"
        grid-template-columns="${x => x.gridTemplateColumns}"
        page-size="${x => x.pageSize}"
    >
        ${x => x.content}
    </fast-data-grid>
`;
function newDataRow(id) {
    return {
        rowId: `rowid-${id}`,
        item1: `value 1-${id}`,
        item2: `value 2-${id}`,
        item3: `value 3-${id}`,
        item4: `value 4-${id}`,
        item5: `value 5-${id}`,
        item6: `value 6-${id}`,
    };
}
function newDataSet(rowCount) {
    return Array.from({ length: rowCount }, (v, i) => newDataRow(`${i + 1}`));
}
export default {
    title: "Data Grid",
    args: {
        rowsData: newDataSet(100),
    },
    argTypes: {
        style: {
            control: { type: "text" },
        },
        content: { table: { disable: true } },
        noTabbing: {
            control: { type: "boolean" },
        },
        generateHeader: {
            options: ["none", "default", "sticky"],
            control: { type: "select" },
        },
        pageSize: {
            control: { type: "number" },
        },
        gridTemplateColumns: {
            control: { type: "text" },
        },
    },
};
export const DataGrid = renderComponent(storyTemplate).bind({});
export const DataGridFixedHeight = renderComponent(storyTemplate).bind({});
DataGridFixedHeight.args = {
    style: "height: 200px; overflow-y: scroll;",
};
