import { FASTElement, html } from "@microsoft/fast-element";
import { CheckableFormAssociated, FormAssociated } from "../form-associated.js";
const template = html `
    <slot></slot>
`;
export class FormAssociatedElement extends FormAssociated(class extends FASTElement {
    constructor() {
        super(...arguments);
        this.proxy = document.createElement("input");
    }
}) {
    constructor() {
        super();
        this.proxy.setAttribute("type", "text");
    }
}
FormAssociatedElement.define({
    name: "test-element",
    template,
});
class CustomInitialValueElement extends FormAssociatedElement {
    constructor() {
        super();
        this.initialValue = "foobar";
        this.proxy.setAttribute("type", "text");
    }
}
CustomInitialValueElement.define({
    name: "custom-initial-value",
    template,
});
export class CheckableFormAssociatedElement extends CheckableFormAssociated(class extends FASTElement {
    constructor() {
        super(...arguments);
        this.proxy = document.createElement("input");
    }
}) {
    constructor() {
        super();
        this.proxy.setAttribute("type", "checkbox");
    }
}
CheckableFormAssociatedElement.define({
    name: "checkable-form-associated",
    template,
});
